<template lang='pug'>
  header.header.navbar.navbar-light.navbar-expand-lg
    .container.px-0
      a.header__logo(href='#')
      button.navbar-toggler(
        type="button"
        data-toggle="collapse"
        data-target="#headerContent"
        aria-controls="headerContent"
        aria-expanded="false"
        aria-label="Toggle navigation"
      )
        span.navbar-toggler-icon

      .layer(v-if='showed')
        .search-layer
          .container
            .field
              InputText.search-layer__input(:placeholder='"Search"')
              Button.search-layer__submit(
                :src='"img/icons/icon-search-btn.svg"'
                :dark='true'
                :text='"Найти"'
                :small='true'
              )
              .search-layer__close(@click='showed = false')

        .darklayer(@click='showed = false')

      .collapse.navbar-collapse#headerContent
        .headerContent
          ul.header__links
            li.link(
              v-for='item in\
              ["О нас", "Наука", "Тренинги", "Материалы", "Новости", "Спец. программы"]'
            )
              a.link__item(href='#') {{ item }}
          SearchIcon.search(@click='showed = true')
          a.chooseLang(href='#') Русский
          a.signup(href='#') Вступить в РОО
</template>

<script>
import InputText from '@components/InputText/InputText';
import Button from '@components/Button/Button';
import SearchIcon from '@public/img/icons/icon-search-nav.svg?inline';

export default {
  components: {
    InputText,
    Button,
    SearchIcon,
  },
  data() {
    return {
      showed: false,
    };
  },
};
</script>

<style lang="scss" scoped>
@import '../../../../assets/styles/layout/header';

$btns-gap:        5px;
$media-gap:       15px;
$chooseLang-icon: url('../../../../../public/img/icons/icon-earth.svg');

.chooseLang {

  &::before {
    background-image: $chooseLang-icon;
  }
}

@media (max-width: 991px) {

  .header {
    flex-wrap: wrap;
    height: unset;
    min-height: $header-min-height;

    &__logo {
      margin-right: 0;
    }

    &__links {
      flex-direction: column;
      align-items: center;
      width: 100%;
    }

    .link {
      padding: $tiny-gap 0;

      &:not(:last-child) {
        margin-right: 0;
      }
    }

    .headerContent {
      flex-direction: column;
    }

    .search,
    .chooseLang,
    .signup {
      margin: $btns-gap;
    }
  }
}

@media (max-width: 1200px) and (min-width: 992px) {

  .header {

    &__logo {
      display: none;
    }
  }
}

@media (max-width: 575px) {

  .header {
    padding-right: $media-gap;
    padding-left: $media-gap;

    .search-layer {

      .field { justify-content: space-between }

      &__input { margin-right: $small-gap }

      &__submit { margin-right: $small-gap }
    }
  }
}
</style>
