<template lang='pug'>
  .card(:class='{\
    card_small: small,\
    card_training: training,\
    card_progs: progs,\
  }')
    .card__header
      .card__city(v-if='city') {{ city }}
      .card__date(v-if='day && month')
        p.date-day {{ day }}
        p.date-month {{ month }}
      img.card__img(:src='src' :alt='alt')
    .card__body
      p.card__info(v-if='info') {{ info }}
      h3.card__heading(:class='{ card__heading_small: smallHeading }') {{ heading }}
      p.card__description {{ text }}
      a.card__link(:class='training ? "goto" : ""' :href='linkHref' v-if='linkText') {{ linkText }}
      Button.card__btn(
        v-if='btnText && progs'
        :dark='btnDark'
        :small='btnSmall'
        :textSmall='btnTextSmall'
        :disabled='btnDisabled'
        :text='btnText'
      ) Перейти
</template>

<script>
import Button from '@components/Button/Button';

export default {
  components: {
    Button,
  },
  props: {
    small: {
      type: Boolean,
    },
    training: {
      type: Boolean,
    },
    progs: {
      type: Boolean,
    },
    city: {
      type: String,
      required: false,
      default: null,
    },
    day: {
      type: Number,
      required: false,
      default: null,
    },
    month: {
      type: String,
      required: false,
      default: null,
    },
    src: {
      type: String,
      required: true,
    },
    alt: {
      type: String,
      default: 'Alt of image',
    },
    info: {
      type: String,
      required: false,
      default: null,
    },
    heading: {
      type: String,
      required: true,
    },
    smallHeading: {
      type: Boolean,
    },
    text: {
      type: String,
      required: false,
      default: 'Some text.',
    },
    linkHref: {
      type: String,
      default: '#',
    },
    linkText: {
      type: String,
      required: false,
      default: null,
    },
    btnDark: {
      type: Boolean,
    },
    btnSmall: {
      type: Boolean,
    },
    btnTextSmall: {
      type: Boolean,
    },
    btnDisabled: {
      type: Boolean,
    },
    btnText: {
      type: String,
      default: 'Button',
    },
  },
};
</script>

<style lang="scss" scoped>
  $date-size:        36px;
  $month-width:      22px;

  $top-alert:        12px;
  $horizontal-alert: 8px;

  $card-context:     20px;
  $standard-context: 18px;
  $img-height:       180px;

  $card-img-width:   130px;
  $card-img-height:  74px;

  $card-btn-width:   128px;
  $card-btn-height:  42px;

  .card {
    padding: 0;

    border: $border-thin solid $border-third-color;
    border-radius: $tiny-border-rd;

    background-color: $light-color;

    &__header {
      position: relative;

      border-top-left-radius: inherit;
      border-top-right-radius: inherit;
    }

    &__city,
    &__date {
      position: absolute;
      top: $top-alert;

      border-radius: $tiny-border-rd;

      color: $light-color;
      text-align: center;

      background-color: $danger-color;
    }

    &__city {
      left: $horizontal-alert;

      padding: $tiny-gap $tiny-gap * 1.5;
    }

    &__date {
      position: absolute;
      right: $horizontal-alert;

      width: $date-size;
      height: $date-size;

      padding: $tiny-gap $tiny-gap / 2;

      .date-day { font-size: $standard-context }

      .date-month {
        width: $month-width;

        margin: 0 auto;
        overflow: hidden;

        font-size: $standard-context - 4;

        white-space: nowrap;
      }
    }

    &__img {
      display: block;
      width: 100%;
      height: $img-height;
      border-radius: inherit;
      object-fit: cover;
    }

    &__body {
      padding: $medium-gap * 1.25;

      border-bottom-right-radius: inherit;
      border-bottom-left-radius: inherit;
    }

    &__info {
      margin-bottom: $small-gap;

      color: $dark-third-color;
      text-transform: uppercase;
    }

    &__heading {
      margin-bottom: $small-gap;

      font-size: $card-context;
      font-family: $bold-font;
      line-height: $card-context * 1.34;

      &_small {
        font-size: $standard-context;
        font-family: $default-font;
      }
    }

    &__description { margin-bottom: $small-gap }

    &__link {
      margin-top: auto;

      color: $info-third-color;
    }
  }

  .card_small {
    display: flex;
    flex-direction: row;

    border: 0;

    background-color: transparent;

    .card__description { display: none }

    .card__link { display: none }

    .card__header { border-radius: inherit }

    .card__img {
      width: $card-img-width;
      height: $card-img-height;
    }

    .card__body {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 0 0 0 $medium-gap;
    }

    .card__heading,
    .card__info { margin: 0 }

    .card__heading { order: 0 }

    .card__info { order: 1 }

    &:not(:last-child) {
      margin-bottom: $medium-gap * 2 - 2;
    }
  }

  .card_training {
    height: 100%;

    box-shadow: $small-shadow $opacity-second-color;

    .card__body {
      display: flex;
      flex-direction: column;
      height: calc(100% - #{$img-height} - #{$medium-gap * 1.25 * 2});

      background-color: $light-third-color;
    }

    .card__info {
      font-size: $tiny-context;
      line-height: $tiny-context * 1.2;
    }

    .card__description {
      color: $opacity-dark-color;
    }

    .card__link {
      width: fit-content;
    }
  }

  .card_progs {

    .card__description {
      color: $dark-third-color;
    }

    .card__btn {
      width: $card-btn-width;
      height: $card-btn-height;
    }
  }

  @media (max-width: 767px) {

    .card {
      margin-bottom: $large-gap;
      padding: 0 !important;
    }
  }
</style>
