<template lang="pug">
  .linkArticle(:class='{ linkArticle_person: person }')
    img.linkArticle__img(:src='src' v-if='person && src')
    .linkArticle__box
      a.linkArticle__heading(:href='linkHref')
        h2.heading {{ heading }}
      .linkArticle__description
        p.linkArticle__date {{ date }}
        p.linkArticle__text {{ text }}
</template>

<script>
export default {
  props: {
    person: {
      type: Boolean,
    },
    src: {
      type: String,
      default: null,
    },
    heading: {
      type: String,
      default: 'Any heading',
    },
    date: {
      type: String,
      default: '1 December 1990',
    },
    text: {
      type: String,
      default: 'Some text.',
    },
    linkHref: {
      type: String,
      default: '#',
    },
  },
};
</script>

<style lang="scss" scoped>
  $img-size: 80px;

  .linkArticle {
    border-bottom: $border-fourth-color solid $border-thin;

    &:not(:last-child) { margin-bottom: $small-gap * 2 }

    &__heading { color: $info-third-color }

    .heading { margin-bottom: $medium-gap }

    &__date,
    &__text {
      display: inline;
      width: fit-content;
    }

    &__date {
      color: $dark-third-color;

      &::after {
        display: inline;

        content: ' | ';
      }
    }

    &__description {
      margin-bottom: $big-gap;
    }
  }

  .linkArticle_person {
    display: flex;
    flex-direction: row;

    &:not(:last-child) { margin-bottom: $medium-gap }

    .linkArticle__img {
      width: $img-size;
      height: $img-size;

      margin-right: $medium-gap;

      border-radius: 50%;
      object-fit: cover;
      object-position: center;
    }

    .heading {
      margin-bottom: $small-gap;
    }

    .linkArticle__date {
      display: none;
    }

    .linkArticle__text {
      color: $dark-third-color;
    }
  }
</style>
