<template lang="pug">
  .advantage
    img.advantage__icon(:src='src' v-if='src')
    h3.advantage__heading
      | {{ heading }}
    p.advantage__description
      | {{ text }}
</template>

<script>
export default {
  props: {
    src: {
      type: String,
      required: false,
      default: null,
    },
    heading: {
      type: String,
      default: 'Advantage',
      required: true,
    },
    text: {
      type: String,
      default: 'Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been.',
      required: true,
    },
  },
};
</script>

<style lang="scss" scoped>
  .advantage {
    box-sizing: border-box;

    &__heading {
      width: fit-content;

      margin-top: $tiny-gap * 3;
      margin-bottom: $tiny-gap * 2;

      color: $dark-color;
      font-family: $bold-second-font;
    }

    &__description {
      width: fit-content;

      color: $opacity-dark-color;
    }
  }

  @media (max-width: 767px) {

    .advantage {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      padding: 0;

      &:not(:last-child) {
        margin-bottom: $large-gap;
      }

      &__heading {
        text-align: center;
      }

      &__description {
        text-align: center;
      }
    }
  }
</style>
