<template lang="pug">
  section.container.px-0.section.qual
    .container
      .row.qual__row
        .col-md-6.qual__col_left.pl-0
          .col-box
            h1.section__heading {{ heading }}
            p.qual__description {{ text }}
          a.qual__link(:href='linkHref') {{ linkText }}
        .col-md-6.qual__col.qual__col_right.pr-0
          img.qual__img(:src='src')
</template>

<script>
export default {
  props: {
    heading: {
      type: String,
      default: 'Any heading',
    },
    text: {
      type: String,
      default: 'Some text',
    },
    linkHref: {
      type: String,
      default: '#',
    },
    linkText: {
      type: String,
      default: null,
    },
    src: {
      type: String,
      default: null,
    },
  },
};
</script>

<style lang="scss" scoped>
  $img-height: 311px;

  .qual {
    height: auto;
    margin-bottom: $large-gap + $big-gap;

    &__col_left {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }

    &__img {
      width: 100%;
      height: $img-height;

      border-radius: $tiny-border-rd;

      object-fit: cover;
    }

    &__link { width: fit-content; }

    .col-box {
      display: flex;
      flex-direction: column;
    }

    &__description {
      color: $dark-third-color;
    }
  }

  @media (max-width: 767px) {

    .qual {

      &__col_left,
      &__col_right {
        padding: 0;
      }

      &__description,
      &__link {
        margin: 0 auto $large-gap;

        text-align: center;
      }
    }
  }
</style>
